  --sidebar-ring: oklch(0.52 0.18 255);
}
.dark {
  --background: oklch(0.1 0 0);
  --foreground: oklch(0.95 0 0);
  --card: oklch(0.14 0 0);
  --card-foreground: oklch(0.95 0 0);
  --popover: oklch(0.14 0 0);
  --popover-foreground: oklch(0.95 0 0);
