  --border: oklch(0.28 0.02 0);
  --input: oklch(0.22 0.02 0);
  --ring: oklch(0.65 0.16 255);
  --chart-1: oklch(0.65 0.16 255);
  --chart-2: oklch(0.62 0.14 160);
  --chart-3: oklch(0.7 0.1 100);
  --chart-4: oklch(0.6 0.15 280);
  --chart-5: oklch(0.68 0.12 200);
  --sidebar: oklch(0.14 0 0);
  --sidebar-foreground: oklch(0.95 0 0);
  --sidebar-primary: oklch(0.65 0.16 255);
  --sidebar-primary-foreground: oklch(0.12 0 0);
  --sidebar-accent: oklch(0.62 0.14 160);
  --sidebar-accent-foreground: oklch(0.95 0 0);
  --sidebar-border: oklch(0.28 0.02 0);
  --sidebar-ring: oklch(0.65 0.16 255);
}
@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
  }
}
